import types from '@/store/types';

export default {
  [types.mutations.SET_LOADING]: (state, value) => {
    state.loading = value;
  },
  [types.mutations.SET_SELECTED_DATE]: (state, value) => {
    state.selectedDate = value;
  },
  [types.mutations.STORE_ALL_TEAMS]: (state, value) => {
    state.teams = value;
  },
  [types.mutations.STORE_ALL_MEMBERS]: (state, value) => {
    state.members = value;
  },
  [types.mutations.STORE_SCHEDULE]: (state, value) => {
    state.schedule = value;
  },
  [types.mutations.STORE_NEWS]: (state, value) => {
    state.news = value;
  },
  [types.mutations.SET_CURRENT_TEAM]: (state, value) => {
    state.selectedTeamId = value;
  },
};
