import axios from 'axios';

import types from '@/store/types';

export default {
  [types.actions.RETRIEVE_MEMBERS]: (context) => {
    context.commit(types.mutations.SET_LOADING, true);
    return axios.get('https://facalendar-api.herokuapp.com/members')
      .then((x) => {
        context.commit(types.mutations.STORE_ALL_MEMBERS, x.data);
      })
      .then(() => context.commit(types.mutations.SET_LOADING, false))
      .catch((e) => {
        console.error(e);
        context.commit(types.mutations.SET_ERROR, true);
      });
  },
  [types.actions.RETRIEVE_TEAMS]: (context) => {
    context.commit(types.mutations.SET_LOADING, true);
    return axios.get('https://facalendar-api.herokuapp.com/')
      .then((x) => {
        context.commit(types.mutations.STORE_ALL_TEAMS, x.data);
      })
      .then(() => context.commit(types.mutations.SET_LOADING, false))
      .catch((e) => {
        console.error(e);
        context.commit(types.mutations.SET_ERROR, true);
      });
  },
  [types.actions.RETRIEVE_SCHEDULE]: (context) => {
    const teamId = context.state.selectedTeamId;
    const date = context.state.selectedDate;
    return axios.get(`https://facalendar-api.herokuapp.com/schedule/${teamId}/${date}`)
      .then((x) => {
        context.commit(types.mutations.STORE_SCHEDULE, x.data);
      })
      .catch((e) => {
        console.error(e);
        context.commit(types.mutations.SET_ERROR, true);
      });
  },
  [types.actions.RETRIEVE_NEWS]: (context) => {
    const teamId = context.state.selectedTeamId;
    const date = context.state.selectedDate;
    return axios.get('https://facalendar-api.herokuapp.com/news')
      .then((x) => {
        context.commit(types.mutations.STORE_NEWS, x.data.data);
      })
      .catch((e) => {
        console.error(e);
        context.commit(types.mutations.SET_ERROR, true);
      });
  },
  [types.actions.SET_TEAM]: (context, teamId) => {
    context.commit(types.mutations.SET_LOADING, true);
    context.commit(types.mutations.SET_CURRENT_TEAM, teamId)
    context.dispatch(types.actions.SET_THEME);
    context.dispatch(types.actions.RETRIEVE_SCHEDULE)
      .then(() => context.dispatch(types.actions.RETRIEVE_NEWS))
      .then(() => context.commit(types.mutations.SET_LOADING, false));
  },
  [types.actions.SET_THEME]: (context) => {
    document.documentElement.style.setProperty('--primary-color', context.getters[types.getters.COLOR_PALETTE].primary);
    document.documentElement.style.setProperty('--light-primary-color-10', context.getters[types.getters.COLOR_PALETTE].light10);
    document.documentElement.style.setProperty('--light-primary-color-20', context.getters[types.getters.COLOR_PALETTE].light20);
    document.documentElement.style.setProperty('--light-primary-color-30', context.getters[types.getters.COLOR_PALETTE].light30);
    document.documentElement.style.setProperty('--light-primary-color-70', context.getters[types.getters.COLOR_PALETTE].light70);
    document.documentElement.style.setProperty('--dark-primary-color-10', context.getters[types.getters.COLOR_PALETTE].dark10);
    document.documentElement.style.setProperty('--dark-primary-color-20', context.getters[types.getters.COLOR_PALETTE].dark20);
    document.documentElement.style.setProperty('--dark-primary-color-30', context.getters[types.getters.COLOR_PALETTE].dark30);
  },
};
