<template>
  <div :class="$style.app">
    <transition name="fade">
      <loading v-if="loading"/>
      <team-selector v-else-if="!selectedTeamId"/>
    </transition>
    <div :class="$style.row">
      <daily-ceremony title="Hoy" :data="DAILY"/>
      <daily-ceremony title="Esta semana" :data="WEEKLY"/>
      <daily-ceremony title="Este mes" :data="MONTHLY"/>
    </div>
    <switch-team/>
    <div :class="$style.row">
      <div :class="$style.twoThirdRow">
        <monthly-calendar/>
      </div>
      <div :class="$style.oneThirdRow">
        <dates-sorted-widget title="Cumpleaos" :elements="BIRTHDAYS"/>
        <dates-sorted-widget title="Aniversarios" :elements="ANNIVERSARIES"/>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState, mapMutations } from 'vuex';
import types from '@/store/types';

import DailyCeremony from '@/components/DailyCeremony';
import DatesSortedWidget from '@/components/DatesSortedWidget';
import MonthlyCalendar from '@/components/MonthlyCalendar';
import SwitchTeam from '@/components/SwitchTeam';
import Loading from '@/components/Loading';
import TeamSelector from '@/components/TeamSelector';

export default {
  name: 'App',
  components: {
    SwitchTeam,
    Loading,
    TeamSelector,
    DatesSortedWidget,
    DailyCeremony,
    MonthlyCalendar,
  },
  data() {
    return {
    };
  },
  computed: {
    ...mapState(['loading', 'selectedTeamId','selectedDate',]),
    ...mapGetters([
      types.getters.BIRTHDAYS,
      types.getters.ANNIVERSARIES,
      types.getters.DAILY,
      types.getters.WEEKLY,
      types.getters.MONTHLY,
    ]),
  },
  methods: {
    ...mapActions([types.actions.RETRIEVE_TEAMS, types.actions.RETRIEVE_MEMBERS]),
    ...mapMutations([]),
  },
  mounted() {
    this.RETRIEVE_TEAMS();
    this.RETRIEVE_MEMBERS();
   },
};
</script>

