<template>
  <!-- <card> -->
    <!-- <template v-slot:title>
      Mes <calendar-icon/>
    </template>
    <template v-slot:body> -->
      <div :class="$style.content">
        <div
          v-for="(d, i) in weekdays" 
          :key="`${i}-pre-d`"
          :class="[$style.dayName]">{{ d }}</div>
        <div
          v-for="(d, i) in preOffset" 
          :key="`${i}-curr-d`"
          :class="[
            $style.day,
            $style.cross,
            [0, 6].includes(d.day()) && $style.weekend,
          ]">
        </div>
        <div
          v-for="(d, i) in dates" 
          :key="`${i}-post-d`"
          :class="[
            $style.day,
            d.isBefore(selectedDate, 'date') && $style.cross,
            d.isSame(today, 'date') && $style.circle,
            d.isSame(selectedDate, 'date') && $style.selected,
            [0, 6].includes(d.day()) && $style.weekend,
          ]">
          <div :class="$style.dayNumber">{{ d.format('D') }}</div>
          <!-- <div
            v-for="event in d.items"
            :key="event.name"
            :class="[
            $style.event,
          ]">
            {{ event.name }}
          </div> -->
        </div>
        <div
          v-for="(d, i) in postOffset" 
          :key="`${i}o-d`"
          :class="[
            $style.day,
            [0, 6].includes(d.day()) && $style.weekend,
          ]">
        </div>
      </div>
    <!-- </template> -->
  <!-- </card> -->
</template>

<script>
import moment from 'moment';
import { mapState, mapMutations } from 'vuex';
import CalendarIcon from 'vue-material-design-icons/CalendarMonth.vue';
import types from '@/store/types';

// import Card from '@/components/Card';

export default {
  name: 'MonthlyCalendar',
  components: {
    // Card,
    CalendarIcon
  },
  methods: {
    ...mapMutations([types.mutations.SET_SELECTED_DATE]),
  },
  computed: {
    ...mapState(['selectedDate']),
    today() {
      return moment();
    },
    dates() {
      const firstDay = moment(this.selectedDate).set('date', 1);
      return [...Array(moment(this.selectedDate).daysInMonth())].map((_, i) => moment(firstDay).add(i, 'd'));
    },
    weekdays() {
      return [...Array(7)].map((x, i) => moment('2020-01-06').add(i, 'days').format('ddd'));
    },
    preOffset() {
      let daysToAdd = this.dates[0].day() - 1;
      if (daysToAdd < 0) {
        daysToAdd = 6;
      }
      return [...Array(daysToAdd)].map((_, i) => moment(this.dates[0].date).add(-i, 'days'));
    },
    postOffset() {
      let daysToAdd = 7 - this.dates[this.dates.length - 1].day();
      if (daysToAdd === 7) {
        daysToAdd = 0;
      }
      return [...Array(daysToAdd)].map((_, i) => moment(this.dates[0]).add(1, 'months').add(i, 'days'));
    },
  },
  data() {
    return {
      feriados: [],
    };
  },
  created() {
    moment.locale('es');
  },
};
</script>

