import moment from 'moment';

import types from '@/store/types';

moment.locale('es');

export default {
  [types.getters.SELECTED_TEAM]: (state) => {
    if (state.selectedTeamId === undefined) {
      return { color: '#478383' };
    }
    return state.teams.find(x => x.id === state.selectedTeamId);
  },
  [types.getters.BIRTHDAYS]: (state) => {
    return state.members
      .filter(x => !!x.birthday)
      .sort((a,b) => moment(a.birthday, 'DD/MM').format('YYYYMMDD') - moment(b.birthday, 'DD/MM').format('YYYYMMDD'))
      .map(x => ({ ...x, date: x.birthday }));
  },
  [types.getters.ANNIVERSARIES]: (state) => {
    return state.members
      .filter(x => !!x.joinAt)
      .map(x => ({ ...x, joinAt: moment(x.joinAt, 'YYYY-MM-DD').format('DD/MM') }))
      .sort((a,b) => moment(a.joinAt, 'DD/MM').format('YYYYMMDD') - moment(b.joinAt, 'DD/MM').format('YYYYMMDD'))
      .map(x => ({ ...x, date: x.joinAt }));
  },
  [types.getters.COLOR_PALETTE]: (state, getters) => {
    const { color } = getters[types.getters.SELECTED_TEAM];
    const shadeColor = (c, p) => {
      let r = parseInt(c.substring(1, 3), 16);
      let g = parseInt(c.substring(3, 5), 16);
      let b = parseInt(c.substring(5, 7), 16);
      r = parseInt(r * (100 + p) / 100);
      g = parseInt(g * (100 + p) / 100);
      b = parseInt(b * (100 + p) / 100);
      r = Math.min(255, r);
      g = Math.min(255, g);
      b = Math.min(255, b);
      const RR = `${r.toString(16).length == 1 ? '0' : ''}${r.toString(16)}`;
      const GG = `${g.toString(16).length == 1 ? '0' : ''}${g.toString(16)}`;
      const BB = `${b.toString(16).length == 1 ? '0' : ''}${b.toString(16)}`;
      return `#${RR}${GG}${BB}`;
    };
    return {
      primary: shadeColor(color, 0),
      light10: shadeColor(color, 5),
      light20: shadeColor(color, 10),
      light30: shadeColor(color, 130),
      light70: shadeColor(color, 150),
      dark10: shadeColor(color, -5),
      dark20: shadeColor(color, -10),
      dark30: shadeColor(color, -15),
    };
  },
  [types.getters.DAILY]: (state) => {
    if (state.schedule === undefined) {
      return undefined;
    }
    return state.schedule.ceremonies.daily.map(x => ({
      ...x,
      selectedImg: state.members.some(y => y.id === x.selected)
        ? state.members.find(y => y.id === x.selected).img
        : undefined,
      selectedName: state.members.some(y => y.id === x.selected)
        ? state.members.find(y => y.id === x.selected).name
        : undefined,
    }));
  },
  [types.getters.WEEKLY]: (state) => {
    if (state.schedule === undefined) {
      return undefined;
    }
    return state.schedule.ceremonies.weekly.map(x => ({
      ...x,
      selectedImg: state.members.some(y => y.id === x.selected)
        ? state.members.find(y => y.id === x.selected).img
        : undefined,
      selectedName: state.members.some(y => y.id === x.selected)
        ? state.members.find(y => y.id === x.selected).name
        : undefined,
    }));
  },
  [types.getters.MONTHLY]: (state) => {
    if (state.schedule === undefined) {
      return undefined;
    }
    return state.schedule.ceremonies.monthly;
  },
};
