<template>
  <card>
    <template v-slot:title>
      {{ title }} <cake-variant-outline-icon/>
    </template>
    <template v-slot:body>
      <div :class="$style.content">
        <div v-if="membersToShow.length > 0" :class="[$style.item]">
          <div :class="[
            $style.photoDivNext,
            moment(membersToShow[0].date, 'DD/MM').isSame(selectedDate, 'day') && $style.birthday,
            ]">
            <img :src="membersToShow[0].img" :alt="membersToShow[0].id" :title="membersToShow[0].name"/>
          </div>
          <div :class="$style.info">
            <div :class="$style.infoBig">
              {{
                moment(membersToShow[0].date, 'DD/MM').isSame(selectedDate, 'day')
                ? 'Es Hoy! :D'
                : moment(membersToShow[0].date, 'DD/MM').add(1, 'd').from(selectedDate)
              }}
            </div>
            <div :class="$style.infoSmall">
              {{ moment(membersToShow[0].date, 'DD/MM').format('dddd') }}
              <br/>
              {{ moment(membersToShow[0].date, 'DD/MM').format('D MMM') }}
            </div>
          </div>
        </div>
        <div
          v-for="(mem, index) in membersToShow.slice(-3)"
          :key="index"
          :class="[$style.item]">
          <div :class="[$style.photoDiv]">
            <img :src="mem.img" :alt="mem.id" :title="mem.name"/>
          </div>
          <div :class="$style.info">
            <div :class="$style.infoBig">
              {{ moment(mem.date, 'DD/MM').add(1, 'd').from(selectedDate) }}
            </div>
            <div :class="$style.infoSmall">
              {{ moment(mem.date, 'DD/MM').format('dddd') }}
              <br/>
              {{ moment(mem.date, 'DD/MM').format('D MMM') }}
            </div>
          </div>
        </div>
        <div :class="[$style.item, $style.clickeable]" @click="toggle">
          <div :class="[$style.photoDiv]">
            <img :src="Hat" alt="others" title="see more"/>
          </div>
          <div :class="$style.info">
            <div :class="$style.infoBig">
              Ver todos
            </div>
          </div>
        </div>
      </div>
      <mi-modal :title="title" v-model="showModal">
        <div :class="$style.rowForModal">
          <div
            v-for="(mem, index) in elements"
            :key="index"
            :class="[
              $style.item,
              moment(mem.date, 'DD/MM').isBefore(selectedDate, 'day') && $style.past,
              ]">
            <div :class="[
              $style.photoDivNext,
              moment(mem.date, 'DD/MM').isSame(selectedDate, 'day') && $style.date,
              ]">
              <img :class="$style.photo" :src="mem.img" :alt="mem.id" :title="mem.name"/>
            </div>
            <div :class="$style.info">
              <div :class="$style.infoBig">
                {{
                  moment(mem.date, 'DD/MM').isSame(selectedDate, 'day')
                  ? 'Es Hoy! :D'
                  : moment(mem.date, 'DD/MM').add(1, 'd').from(selectedDate)
                }}
              </div>
              <div :class="$style.infoSmall">
                {{ moment(mem.date, 'DD/MM').format('dddd') }}
                <br/>
                {{ moment(mem.date, 'DD/MM').format('D MMM') }}
              </div>
            </div>
          </div>
        </div>
      </mi-modal>
    </template>
  </card>
</template>

<script>
import moment from 'moment';
import { mapState } from 'vuex';

import CakeVariantOutlineIcon from 'vue-material-design-icons/CakeVariantOutline.vue';
import Hat from '@/assets/hat.webp';

import Card from '@/components/Card';
import MiModal from '@/components/Modal';

export default {
  name: 'Birthdays',
  components: {
    Card,
    CakeVariantOutlineIcon,
    MiModal,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    elements: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      Hat,
      showModal: false,
    };
  },
  computed: {
    ...mapState(['selectedDate']),
    membersToShow() {
      return this.elements
        .filter(x => moment(x.date, 'DD/MM').isSameOrAfter(this.selectedDate, 'day'))
        .filter((_, i) => i < 4);
    },
  },
  methods: {
    moment,
    toggle() {
      this.showModal = !this.showModal;
    },
  },
  created() {
    moment.locale('es');
  },
};
</script>

<style module lang="scss">
