  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: baseline;
  margin: 0 1em 0.75em;
}
.item {
  display: flex;
  flex-direction: column;
  margin: 0.75em 0.25em 0;
  align-items: center;
  position: relative;
  flex-basis: 3%;
}
.photoDivNext {
  margin: 0;
  height: 100px;
  width: 100px;
  padding: 0;
  & img {
    height: 100px;
    width: 100px;
    // border-radius: 10%;
    box-shadow: 0 0.1px 0.1em var(--dark-primary-color-30);
    // border: 0.2em solid var(--light-primary-color-30);
    box-sizing: border-box;
  }
}
.photoDiv {
  margin: 0;
  height: 60px;
  width: 60px;
  padding: 0;
  opacity: 0.5;
  & img {
    height: 60px;
    width: 60px;
    // border-radius: 10%;
    border: 1px solid var(--light-primary-color-30);
    box-sizing: border-box;
  }
}
.past {
  opacity: 0.3;
  filter: grayscale(1);
  & .infoBig {
    display: none;
  }
}
.birthday {
  position: relative;
  &::after{
    background: url("../assets/hat.webp") no-repeat scroll 0 0 transparent;
    background-size: cover;
    content: " ";
    display: block;
    position: absolute;
    height: 4em;
    width: 4em;
    left: 20px;
    top: -32px;
    z-index: 1;
    animation: dance 1s infinite;
  }
}
.info {
  display: flex;
  flex-direction: column;
  color: black;
  background: var(--light-primary-color-30);
  transition: background-color 0.5s linear;
  text-align: center;
  font-style: italic;
  justify-content: center;
  box-shadow: 0 0.2px 0.001em gray;
}
.infoBig {
  font-size: 0.7em;
  line-height: 1em;
  padding: 0 0.25em;
}
.infoSmall {
  display: flex;
  font-size: 0.5em;
  font-style: italic;
  flex-direction: column;
  justify-content: center;
  line-height: 1em;
  background-color: var(--dark-primary-color-30);
  transition: background-color 0.5s linear;
  padding: 2px 7px;
}
.rowForModal {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
@keyframes dance{
  0%{
    transform: scale(1.05) rotate(10deg);
  }
  50%{
    transform: scale(0.995) rotate(0deg);
  }
  100%{
    transform: scale(1.05) rotate(10deg);
  }
}
.clickeable {
  cursor: pointer;
  &:hover{
    & > * {
      opacity: 1 !important;
    }
  }
}
</style>